header {
    .container {
        .sub_header {
            max-width: 90%;
            margin: auto;
            display: flex;
            flex-direction: row;
            align-items: baseline;
            justify-content: space-between;
            padding-top: 30px;
            padding-bottom: 30px;
            position: relative;
            background: hsl(0, 0%, 100%);
            .header_navigation, .request_invite {
                display: none;
            }
            .header_input {
                position: absolute;
                top: 22px;
                right: 6px;
                z-index: 1;
                width: 30px;
                height: 30px;
                opacity: 0;
            }
            .header_input:checked ~ .header_navigation {
                width: 70%;
                display: inline-block;
                padding: 16px;
                background: white;
                border-radius: 5px;
                position: fixed;
                top: 90px;
                margin: auto;
                left: 10%;
                box-shadow: 0px 0px 10px grey;
                ul {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    li {
                        padding: 10px 0;
                        a {
                            font-family: "PublicSans-Regular";
                            font-style: normal;
                            font-weight: 500;
                            color: rgb(50, 44, 44);
                            font-size: 16px;
                            position: relative;
                            display: inline-block;
                            text-decoration: none; 
                            }
                            a:hover {
                                background-position: 0 100%;
                                font-weight: 700;
                        }
                    }
                }
            }
            .open_menu svg {
                position: absolute;
                top: 34.5px;
                right: 13px;
                cursor: pointer;
            }
            .close_menu svg {
                display: none;
            }
            .header_input:checked ~ .open_menu svg{
                display: none;
            }
            .header_input:checked ~ .close_menu svg{
                display: inline-block;
                position: absolute;
                top: 30px;
                right: 13px;
                cursor: pointer;
            }
        }
        .header_details {
            padding-bottom: 80px;
            background-color: hsl(0, 0%, 98%);
            background-image: url();
            .container {
                display: flex;
                flex-direction: column;
                align-items: center;
                .images {
                    background-image: url(../images/bg-intro-mobile.svg);
                    background-repeat: no-repeat;
                    background-position: 50% 50%;
                    background-size: cover;
                    img {
                        margin-top: -17%;
                        width: 100%;
                    }
                }
                div:nth-of-type(2) {
                    max-width: 90%;
                    margin: auto;
                    h1 {
                        font-family: "PublicSans-Regular";
                        font-style: normal;
                        font-weight: 500;
                        font-size: 40px;
                        color: hsl(233, 26%, 24%);
                        padding-top: 30px;
                        padding-bottom: 30px;
                    }
                    p {
                        font-family: "PublicSans-Regular";
                        font-style: normal;
                        font-weight: 500;
                        font-size: 18px;
                        color: hsl(233, 8%, 62%);
                        opacity: .8;
                        padding-bottom: 30px;
                    }
                    .request_invite {
                        display: block;
                        text-align: center;
                        color: hsl(0, 0%, 100%);
                        background: linear-gradient(to right, #1de027, #4ca2cb) !important;
                        outline: none;
                        padding: 13px 20px;
                        box-sizing: border-box;
                        border: none;
                        cursor: pointer;
                        border-radius: 25px;
                        white-space: nowrap;
                        font-family: "PublicSans-Regular";
                        font-style: normal;
                        font-weight: 500;
                        font-size: 18px;
                    }
                    .request_invite:hover{
                        background: linear-gradient(to right, #8dcb90, #85afc2) !important;
                    }
                }
            }
        }
    }
}

@media(min-width: 820px) {
    header {
        .container {
            .sub_header {
                max-width: 80%;
                margin: auto;
                display: flex;
                flex-direction: row;
                align-items: center;
                padding-top: 0;
                padding-bottom: 0;
                .header_navigation, .request_invite {
                    display: inline-block;
                }
                .header_input {
                    display: none;
                }
                .header_navigation {
                    position: initial;
                    display: inline-block;
                    background: white;
                    margin: auto;
                    width: 335px;
                    ul {
                        display: none;
                        display: flex;
                        flex-direction: row;
                        li {
                            padding: 0;
                            font-family: "PublicSans-Regular";
                            font-style: normal;
                            font-weight: 500;
                            a {
                                font-size: 18px;
                                display: inline-block;
                                font-weight: 500;
                                padding: 40px 0;
                                color: hsl(233, 8%, 62%);
                                text-decoration: none; 
                                cursor: pointer;
                                }
                                a:hover {
                                    color: hsl(233, 26%, 24%);
                                    border-bottom: 4px solid #1de427;
                            }
                        }
                        li:not(:last-of-type) {
                            margin-right: 16px;
                        }
                    }
                }   
                .open_menu svg {
                    display: none;
                }
                .close_menu svg {
                    display: none;
                }
                .request_invite {
                    display: block;
                    text-align: center;
                    color: hsl(0, 0%, 100%);
                    background: linear-gradient(to right, #1de027, #4ca2cb) !important;
                    outline: none;
                    padding: 13px 20px;
                    box-sizing: border-box;
                    border: none;
                    cursor: pointer;
                    border-radius: 25px;
                    white-space: nowrap;
                    font-family: "PublicSans-Regular";
                    font-style: normal;
                    font-weight: 500;
                    font-size: 18px;
                }
                .request_invite:hover{
                    background: linear-gradient(to right, #8dcb90, #85afc2) !important;
                }
            }
            .header_details {
                padding-bottom: 0;
                .container {
                    max-width: 100%;
                    display: flex;
                    flex-direction: row-reverse;
                    .images {
                        background-image: url(../images/bg-intro-desktop.svg);
                        background-repeat: no-repeat;
                        background-size: 80vw;
                        background-position: 14% 80%;
                        img {
                            width: 95%;
                            margin-top: -16%;
                            margin-left: 19%;
                            margin-bottom: -15%;
                        }
                    }
                    div:nth-of-type(2) {
                        margin-left: 10%;
                        margin-top: 0;
                        padding-top: 80px;
                        h1 {
                            max-width: 400px;
                            margin-right: auto;
                        }
                        p {
                            max-width: 500px;
                            margin-right: auto;
                        }
                    }
                }
            }
        }
    }
}